"use client";

import React from "react";
import { Icon, Input, InputGroup } from "@chakra-ui/react";
import { link } from "@/assets/svgs";

export const InputUrl = ({ name, value, onChange, placeholder, width }) => {
  return (
    <InputGroup startElement={<Icon>{link.icon}</Icon>}>
      <Input
        name={`${name}`}
        value={value ?? ""}
        onChange={onChange}
        placeholder={placeholder}
        // value={item?.venueLocation}
        // onChange={(e) =>
        //   handleInputChange(venueKey, "venueLocation", e.target.value)
        // }
        // placeholder={t("location")}

        variant="subtle"
        borderRadius="4px"
        bg="#F9FAFB"
        w={width}
        h="52px"
      />
    </InputGroup>
  );
};
